* {
    margin: 0;
    padding: 0;
}

body {
    background-color: #fafafa;
}

html {
    overflow-y: scroll;
    /* for IE8 */
}

:root {
    overflow-y: auto;
    overflow-x: hidden;
}

:root body {
    position: absolute;
}

body {
    width: 100vw;
    overflow: hidden;
}

.App {
    width: 900px;
    margin: 0 auto;
    padding-top: 30px;
    background-color: pink;
    background-color: #fff;
}

.active {
    background-color: #1677ff;
    color: #eff6ff;
}

.active:hover {
    background-color: #4096ff !important;
    color: #f9fcff !important;
}


.header {
    margin-bottom: 20px;
}

.middle {
    margin-bottom: 20px;
}

.itemList {
    display: flex;
    flex-wrap: wrap;
}

.itemList .cardItem {
    width: 200px;
    box-sizing: border-box;
    margin: 5px 23px 5px 10px;
    position: relative;
}

.itemList .cardItem:nth-child(4n+1) {
    margin-left: 0;
}

.itemList .cardItem:nth-child(4n) {
    margin-right: 0;
}

.itemList .cardItem .card {
    width: 100%;
}

.itemList .cardItem .card img {
    width: 200px;
    height: 300px;
}

.itemList .cardItem .card .ant-card-body {
    padding-left: 0;
    padding-right: 0;
}

.itemList .cardItem .card p {
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.itemList .cardItem .tag {
    position: absolute;
    top: -2px;
    left: -2px;
    /* width: 30px; */
    /* height: 40px; */
    /* background-color: #575757; */
    text-align: center;
    border: 20px solid red;
    /* font-size: 20px; */
    /* line-height: 35px; */
    width: 0;
    height: 0;
    border: 15px solid #575757;
    border-bottom: 10px solid transparent;
    border-top: 30px solid #575757;
    /* border-top: 3px solid #000; */
    /* border-left: 3px solid #000; */
    color: #fff;
    cursor: pointer;
}

.itemList .cardItem .tag span {
    position: absolute;
    width: 20px;
    text-align: center;
    top: -11px;
    left: -5px;
}

.dreaP p {
    line-height: 30px;
}